	module.exports = {"data":{"site":{"siteMetadata":{"title":"Blog by Michael Lancaster","subtitle":"Software Engineer focused in Javascript applications","copyright":"© All rights reserved.","author":{"name":"Michael Lancaster","twitter":"weblancaster"},"url":"http://www.bymichaellancaster.com"}},"markdownRemark":{"id":"/Users/michaellancaster/Sites/github/michaellancaster-blog/src/pages/articles/2013-12-12-playstation4-alert-using-best-buy-api-nodejs-socketio-nedb/index.md absPath of file >>> MarkdownRemark","html":"<p>Long story short.. I didn’t want to check Best Buy every day to see if PS4 is available..\nSo I built this Web Application <a href=\"http://ps4alerts.herokuapp.com/\" target=\"_blank\" title=\"Be notified when PlayStation 4 is available at Bes Buy\">PS4 Alerts!</a> to check every one (1) minute Best Buy Data and if find any PS4 available in store or online will send me an email notification with a direct link to the product.</p>\n<p>I also added one feature as subscription so more people can be notified as well.</p>\n<h3>Specs</h3>\n<ul>\n<li>Node.js/Express.js (Backend/Server)</li>\n<li>Socket.io (Real time communication with the client)</li>\n<li>Handlebars.js (as backend template view)</li>\n<li>NeDB (Database)</li>\n<li><a href=\"https://bbyopen.com/\" target=\"_blank\" title=\"Best Buy open API\">BBYOpen API</a></li>\n</ul>\n<p>Be advised I built it in two nights..Enjoy!</p>","fields":{"date":"2013-12-12"},"frontmatter":{"title":"PS4 Alerts using BBY API","description":"Long story short.. I didn't want to check Best Buy every day to see if PS4 is available..So I built this Web Application to check every one (1) minute Best Buy Data and if find any PS4 available in store or online will send me an email notification with a direct link to the product."}}},"pathContext":{"slug":"/articles/2013-12-12-playstation4-alert-using-best-buy-api-nodejs-socketio-nedb/"}}

